html{overflow:scroll;overflow-x:hidden}html body{background:transparent linear-gradient(180deg, #053354 0, #006aa5 100%) 0 0 no-repeat padding-box;margin:0;font-family:'Raleway', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}::-webkit-scrollbar{width:0px;background:transparent}::-webkit-scrollbar-thumb{background:#FF0000}

.main-banner{margin-top:40px}.main-banner .hello{color:#f5f5f5;font-size:60px;font-weight:bold}.main-banner .subHeader{color:#f5f5f5;font-size:25px;font-weight:bold}.main-banner .txt-rotate>.wrap{border-right:0.08em solid #666}@media screen and (max-width: 500px){.main-banner{margin-top:10px}.main-banner .hero-image{width:50%}.main-banner .hello{font-size:50px;line-height:48px;margin:12px 0}.main-banner .subHeader{font-size:30px;line-height:45px;font-weight:400}}img.my-avatar,img.my-half{width:100%;opacity:0.75;border-radius:50%;border:24px solid #f5f5f5}img.hero-image{width:20%}@media screen and (max-width: 500px){img.hero-image{width:65%}}

header{position:-webkit-sticky;position:sticky;border-bottom:2px solid #f5f5f5;padding:24px 0 !important}header .header-main{color:whitesmoke;font-size:24px;font-weight:400;text-align:center}header .header-main .header-button{background-color:transparent;color:#f5f5f5;margin:8px;text-decoration:none;border-radius:8px;padding:8px;font-size:18px;border:1px solid #f5f5f5}header .header-main .header-button:hover,header .header-main .header-button:focus{background-color:#f5f5f5;color:black}header .side-bar{text-align:end;margin:auto}header .side-bar .btn{color:#f5f5f5;font-size:24px;background-color:#f5f5f5;border:none;margin:8px}@media screen and (max-width: 500px){header .side-bar .btn{display:block;text-align:left}}header .small-logo{text-align:left;padding:0 16px;font-size:30px;font-weight:bold;color:whitesmoke}header .nochange{text-align:end;margin:auto}header .nochange .bar1,header .nochange .bar2,header .nochange .bar3{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;transition:0.4s}header .nochange .header-button{display:none}header .change{text-align:end;margin:auto}header .change .bar1{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-2px, 6px);transition:0.4s}header .change .bar2{opacity:0}header .change .bar3{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(0px, -4px);transition:0.4s}header .change .wrapper{margin:24px 0;text-align:center}header .change .wrapper .header-button{background-color:transparent;color:#f5f5f5;margin:8px;text-decoration:none;border-radius:8px;padding:8px;font-size:18px;border:1px solid #f5f5f5}header .change .wrapper .header-button:hover,header .change .wrapper .header-button:focus{background-color:#f5f5f5;color:black}@media screen and (max-width: 500px){header{padding:16px 0 !important;margin-bottom:8px}}

.about-text{text-align:left}.about-text .subHeader{padding:8px 0}.my-image{border-style:groove}.skills .circular{margin-top:60px}.skills .circular .circle-anim{width:150px;padding:0 8px}.skills .circular .circle-text{margin-top:8px;color:#f5f5f5;font-size:28px}.skills h2.subheader{color:#f5f5f5;text-align:left;font-size:28px;line-height:180%}

.contact-info .input-name{color:whitesmoke;text-align:left}@media screen and (max-width: 500px){.contact-info .input-name{text-align:center}}.contact-info .alert-heading.h4{margin:0}.contact-info .success{color:green}.contact-info .error{color:red}.contact-info-header{margin:60px 0}.contact-info-header .hello{color:#f5f5f5;font-size:60px;font-weight:bold}.contact-info-header .subHeader{color:#f5f5f5;font-size:25px;font-weight:bold}@media screen and (max-width: 500px){.contact-info-header .hello{font-size:50px;line-height:48px;margin:12px 0}.contact-info-header .subHeader{font-size:30px;line-height:45px;font-weight:400}}.contact-info button.submit-button.btn.btn-primary,.contact-info button.btn.btn-primary{background-color:transparent;color:#f5f5f5;margin:8px;text-decoration:none;border-radius:8px;padding:8px 24px;font-size:18px;border:1px solid #f5f5f5}.contact-info button.submit-button.btn.btn-primary:hover,.contact-info button.submit-button.btn.btn-primary:focus,.contact-info button.btn.btn-primary:hover,.contact-info button.btn.btn-primary:focus{background-color:#f5f5f5;color:black}

footer{color:whitesmoke;position:absolute;border-top:2px solid whitesmoke;background:#006aa5;width:100%}footer .rights{margin:8px 0;text-align:center}footer h1{font-size:55px}footer h2{font-size:35px}footer a{margin:8px}footer .side-bar{text-align:end;margin:8px 0}footer .side-bar img{color:whitesmoke;font-size:24px;font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;background-color:transparent;border:none;margin:8px}@media screen and (max-width: 500px){footer .side-bar{text-align:center}}

.App{text-align:center;position:relative;min-height:100vh}.App .main-container{padding-bottom:2.5rem}

